.top-background {
    background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
}

.top {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    flex-direction: row;
    min-height: 455px;
    position: relative;
    z-index:-1;
}

$border-style: 1px solid #686868bd;

.container {
    display: flex;
    justify-content: center;
    .profile-back:before {
        content: "";
        position: absolute;
        border-radius: 10px;
        inset: -5px;
        /* control the spread */
        transform: translate(10px, 8px);
        /* control the offsets */
        z-index: -1;
        /* place the element behind */
        background-image: linear-gradient(to top, #accbee 0%, #e7f0fd 100%);
        filter: blur(10px);
        /* control the blur */
    }

    
    .profile-back {
        position: relative;
        bottom: 30px;
        background-color: white;
        border: $border-style;
        border-radius: 10px;
        max-width: 1024px;
        min-width: 512px;
       
        .profile-content {
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;
            bottom: 50px;
            img {
                width: 100px;
                height: 100px;
                border-radius: 50%;
                border: $border-style;

            }
        }
    }


}